/**=====================
    2.30 Table CSS Start
==========================**/

thead,
tbody,
tfoot,
tr,
td,
th {
	border-color: #dee2e6;
}
.table> :not(caption)>*>* {
	background-color: transparent;
}
.pills-component {
	width: 20%;
}

#row_create {
	td.highlight {
		font-weight: 600;
		color: $primary-color;
	}

	td.danger {
		font-weight: 600;
		color: $danger-color;
	}
}

.table {
	margin-bottom: $table-b-margin;

	> :not(:last-child) {
		> :last-child {
			>* {
				border-bottom-color: $light-gray;
			}
		}
	}

	th,
	td {
		padding: $table-padding;
	}

	&.table-primary {
		th {
			color: $white;
		}
	}

	thead.bg-primary {
		tr {
			th {
				color: $white;
			}
		}
	}

	th {
		color: $table-heading-color;
		font-weight: 700;

		&.f-light {
			color: var(--chart-text-color);
			opacity: 0.8;
		}
	}

	td {
		color: $table-row-color;
	}

	[class*='bg-'] {
		color: $white;
	}

	.bg-light {
		color: $theme-body-font-color;
	}

	tfoot {
		font-weight: $table-footer-font-weight;
	}
}

.table-dark {
	th {
		color: $white;
	}
}

.table-inverse {
	color: $white;
	background-color: $table-inverse-bg-color;

	th,
	td {
		border-color: $white;
		color: $white;
	}

	&.table-bordered {
		border: 0;
	}
}

.table[class*='bg-'] {

	th,
	td {
		color: $white;
	}
}

.table-hover {
	>tbody {
		>tr:hover {
			--bs-table-accent-bg: #f6f6f6;
		}
	}

	tbody {
		tr {
			&:hover {
				background-color: rgba($primary-color, 0.1);
			}

			vertical-align: middle;

			td {
				svg {
					width: 30px;
					height: 30px;
					padding: 5px;
					border-radius: 4px;
					margin-right: 10px;

					[dir="rtl"] & {
						margin-right: unset;
						margin-left: 10px;
					}
				}
			}
		}
	}
}

// Table border-bottom removes in particular typography page
.typography-table {
	tr {
		&:last-child {
			border-bottom: none;

			td {
				border-bottom: none;
			}
		}
	}
}

.table-double {
	border-left: 4px double #dee2e6;
	border-right: 4px double #dee2e6;

	thead,
	tbody,
	tfoot,
	tr,
	td,
	th {
		border-style: double;
		border-width: 4px;
		border-right: 0;
		border-left: 0;
	}
}

.table-dotted {
	border-left: 2px dotted #dee2e6;
	border-right: 2px dotted #dee2e6;

	thead,
	tbody,
	tfoot,
	tr,
	td,
	th {
		border-style: dotted !important;
		border-width: 2px;
		border-right: 0;
		border-left: 0;
	}
}

.table-dashed {
	border-left: 2px dashed #dee2e6;
	border-right: 2px dashed #dee2e6;

	thead,
	tbody,
	tfoot,
	tr,
	td,
	th {
		border-style: dashed !important;
		border-width: 2px;
		border-right: 0;
		border-left: 0;
	}
}

//Sizing class
.table-xl {

	td,
	th {
		padding: $table-xl-padding;
		font-size: $table-xl-font;
	}
}

.table-lg {

	td,
	th {
		padding: $table-lg-padding;
		font-size: $table-lg-font;
	}
}

.table-de {

	td,
	th {
		padding: $table-de-padding;
	}
}

.table-sm {

	th,
	td {
		padding: $table-sm-padding;
		font-size: $table-sm-font;
	}
}

.table-xs {

	th,
	td {
		padding: $table-xs-padding;
		font-size: $table-xs-font;
	}
}

//Table Border
.table-border-horizontal {

	tr,
	th,
	td {
		border-top: 1px solid $horizontal-border-color;
		padding: $horizontal-padding;
	}
}

.table-border-vertical {

	tr,
	th,
	td {
		border-top: 0;
		border-right: 1px solid $horizontal-border-color;
	}

	tr {
		td {
			border-bottom: 0;

			&:last-child {
				border-right: none;
			}
		}
	}

	tbody {
		tr {
			th {
				border-bottom: 0;
			}
		}
	}
}

.table-bordernone {
	td {
		border: none !important;
	}

	thead {
		th {
			border: none !important;
		}
	}

	.u-s-tb {
		padding: 11px;
	}
}

.table-striped {
	tbody {
		tr {
			&:nth-of-type(odd) {
				background-color: var(--light-background);
				--bs-table-accent-bg: unset;
			}
		}
	}
}

.table-bordered {

	thead,
	tbody,
	tfoot,
	tr,
	td,
	th {
		border-color: rgba($light-text, 0.3);
	}
}
.product-table {
	.dataTable {
		thead {
			tr {
				th {
					&:nth-child(4){
						min-width: 70px;
					}
					&:nth-child(5){
						min-width: 80px;
					}
				}
			}
		}
		tbody {
			tr {
				td {
					&:nth-child(2){
						min-width: 200px;
					}
				}
			}
		}
	}
}
.order-history {
	.dataTable {
		thead {
			tr {
				th {
					&:nth-child(2){
						min-width: 125px;
					}
					&:nth-child(3){
						min-width: 60px;
					}
					&:nth-child(4){
						min-width: 65px;
					}
					&:nth-child(5){
						min-width: 130px;
					}
				}
			}
		}
	}
}
.table-striped {
	> tbody {
		> tr {
			&:nth-of-type(odd) > * {
				background-color:rgba($primary-color,0.1) !important;
				--bs-table-accent-bg:unset;
			}
		}
	}
}
.invoice {
	p {
		color: $theme-body-sub-title-color;
	}
}
.invoice-table {
	tbody {
		tr {
			&:nth-child(odd){
				background-color: rgba($primary-color, 0.1);
				--bs-table-bg-type: unset;
			}
			td{
				&:nth-child(1){
					min-width: 200px;
				}
			}
		}
	}
}
.aos-table {
	thead {
		tr {
			th {
				&:nth-child(2){
					min-width: 200px;
				}
			}
		}
	}
}

/**=====================
    2.30 Table CSS Ends
==========================**/