/**=====================
   3.34 Progress CSS Start
==========================**/
.progress-showcase {
	.progress {
		margin-bottom: 10px;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.sm-progress-bar {
	height: 8px;
}

.lg-progress-bar {
	height: 20px;
}

.progress-number {
	button {
		width: 2rem;
		height: 2rem;
	}

	.progress-wrapper {
		height: 1px;

		&.progress {
			background-color: var(--recent-dashed-border);
		}

		.progress-bar {
			background-color: var(--theme-deafult);
		}
	}
}

.progress-btn {
	background-color: $light-chartist-grid-color;
	border-color: $light-chartist-grid-color;
}

.small-progressbar {
	position: relative;
	overflow: visible;

	&::after {
		position: absolute;
		content: '';
		top: -2px;
		right: -9px;
		background-color: $primary-color;
		width: 12px;
		height: 12px;
		border-radius: 100%;

		[dir="rtl"] & {
			left: -9px;
			right: unset;
		}
	}

	.progress-label {
		position: absolute;
		top: -26px;
		right: -6px;
		font-weight: 700;

		[dir="rtl"] & {
			right: unset;
			left: -6px;
		}
	}

	.animate-circle {
		position: absolute;
		top: -11px;
		right: -18px;
		height: 30px;
		width: 30px;
		border: 14px solid $primary-color;
		border-radius: 70px;
		animation: heart 1s ease-in-out;
		animation-iteration-count: infinite;

		[dir="rtl"] & {
			right: unset;
			left: -18px;
		}
	}
}

@keyframes heart {
	0% {
		transform: scale(0);
		opacity: 0;
	}

	25% {
		transform: scale(.1);
		opacity: 1;
	}

	50% {
		transform: scale(.5);
		opacity: .3;
	}

	75% {
		transform: scale(.8);
		opacity: .5;
	}

	100% {
		transform: scale(1);
		opacity: 0;
	}
}

.progress-b-space {
	.progress {
		margin-bottom: 35px;
	}
}
@each $progress-gradient-name,
  $progress-gradient-color in (primary, var(--theme-deafult)),
  (secondary, $secondary-color), (success, $success-color),
  (danger, $danger-color), (info, $info-color), (light, $light-color),
  (dark, $dark-color), (warning, $warning-color)
{
  .progress-gradient-#{$progress-gradient-name} {
    background-image: linear-gradient(
      to right,
      $transparent-color,
      $progress-gradient-color
    );
    position: relative;
    &:hover {
      background-size: 100%;
    }
    &::after {
      position: absolute;
      content: "";
      top: -1px;
      right: -1px;
      background-color: $progress-gradient-color;
      width: 10px;
      height: 10px;
      border-radius: 100%;
      box-shadow: 0 0 17px 4px rgba($progress-gradient-color, 0.36);
    }
    .animate-circle {
      position: absolute;
      top: -9px;
      right: -8px;
      height: 25px;
      width: 25px;
      border: 5px solid $progress-gradient-color;
      border-radius: 70px;
      animation: heartbit 1s ease-out;
      animation-iteration-count: infinite;
    }
  }
}
@keyframes heartbit {
	0% {
	  transform: scale(0);
	  opacity: 0;
	}
	25% {
	  transform: scale(0.1);
	  opacity: 0.1;
	}
	50% {
	  transform: scale(0.5);
	  opacity: 0.3;
	}
	75% {
	  transform: scale(0.8);
	  opacity: 0.5;
	}
	100% {
	  transform: scale(1);
	  opacity: 0;
	}
  }
/**=====================
   3.34 Progress CSS Ends
==========================**/