/**=====================
  5.2 Grid CSS Start
==========================**/
.grid-showcase {
  margin-bottom: -10px;

  span {
    display: block;
    padding: 10px;
    margin-bottom: 10px;
    text-align: center;
    color: $primary-color;
    background-color: rgba($primary-color, 0.05) !important;
  }

}

.grid-align {
  margin-bottom: -16px;

  .row {
    padding: 15px 0;
    min-height: 7rem;
    margin-bottom: 16px;
  }
}

.grid-vertical {
  min-height: 150px;
  padding: 10px;
  color: $black;

}

/**=====================
    5.2 Grid CSS Ends
==========================**/